import axios from 'axios'
import Vue from 'vue'
import moment from 'moment'
import iputils from '@/js/iputils'

import qs from 'qs'

// axios https://www.kancloud.cn/yunye/axios/234845

// 30S
axios.defaults.timeout = 30000

// let pathname = (location.pathname || '').toLowerCase()
// let subdomain = iputils.islocal() && pathname.indexOf('/travel/supergoproduct') !== 0 ? '' : '/travel/supergoproduct'
// //  `url`  `url`  URL
// axios.defaults.baseURL = location.host + subdomain

// 
axios.defaults.headers['X-Requested-With'] = XMLHttpRequest

// 
axios.defaults.withCredentials = true

// post  content-type
// axios.defaults.headers['post']['Content-Type'] = `application/x-www-form-urlencoded; charset=UTF-8`


// this.$moment http://momentjs.cn/
Vue.prototype.$moment = moment

let pending = []; //ajaxajax
let cancelToken = axios.CancelToken;
/**
 * http
 * @param {*} config 
 */
function removePending(config) {
  for (let p in pending) {
    if (pending[p].key === getHttpKey(config)) {
      pending[p].cancel("repeating"); // 
      pending.splice(p, 1); //
      console.log('http');
    }
  }
}
/**
 * httpkeyurl+method+params
 * @param {*} config 
 */
function getHttpKey(conf) {
  let param;
  if (conf.method === 'post') {
    param = conf.data;
  } else {
    param = qs.stringify(conf.params);
  }
  return conf.url + conf.method + param;
}

/**
 * axios 
 */
axios.interceptors.request.use(config => {
  removePending(config); //ajax
  config.cancelToken = new cancelToken((c) => {
    // ajax&
    pending.push({
      key: getHttpKey(config),
      cancel: c
    });
  });
  return config;
}, err => {
  return Promise.reject(err)
});

/**
 * axios 
 */
axios.interceptors.response.use(res => {
  removePending(res.config); //ajaxpending
  return res
}, error => {
  if (error && error.message === 'repeating') {
    return {}; // 
  }
  return Promise.reject(error)
})

export default {
  /**
   * get 
   * @param {*} url 
   * @param {*} param 
   */
  get(url, param) {
    return new Promise((resolve, reject) => {
      axios({
        method: 'get',
        url,
        params: param
      }).then(res => {
        resolve(res.data)
      }).catch(reject)
    })
  },
  /**
   * post 
   * @param {*} url 
   * @param {*} param 
   */
  post(url, param) {
    return new Promise((resolve, reject) => {
      axios({
        method: 'post',
        url,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        },
        data: qs.stringify(param)
      }).then(res => {
        resolve(res.data)
      }).catch(reject)
    })
  },
  /**
   * post json 
   * @param {*} url 
   * @param {*} param 
   */
  postjson(url, param) {
    return new Promise((resolve, reject) => {
      axios({
        method: 'post',
        url,
        headers: {
          'Content-Type': 'application/json; charset=utf-8'
        },
        data: param
      }).then(res => {
        resolve(res.data)
      }).catch(reject)
    })
  }
}



